html, body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@font-face {
  font-family: 'Righteous';
  src: local('Righteous'), url(../fonts/Righteous-Regular.ttf) format('truetype');
}

@font-face {
  font-family: 'FugazOne';
  src: local('FugazOne'), url(../fonts/FugazOne-Regular.ttf) format('truetype');
}

@font-face {
  font-family: 'Bree Serif';
  src: local('Bree Serif'), url(../fonts/BreeSerif-Regular.ttf) format('truetype');
}

/*******************************************************************/

.wrapper {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  display: flex;
  flex-flow: row;
  /*background: linear-gradient(135deg, #9ce3ff, #0099ff);*/
}

.home-wrapper {
  width: calc(100vw * 1 / 3);
  height: 100vh;
}

.information-wrapper {
  width: calc((100vw) * 2 / 3);
  height: 100vh;
  display: flex;
  flex-flow: row wrap;
  overflow-y: overlay;
}


.no-align {
  align-items: unset !important;
}

.align {
  align-items: center !important;
}

.align-center {
  text-align: center !important;
}

.align-left {
  text-align: left;
}

.justify {
  justify-content: center !important;
}

.flex-row {
  display: flex;
  flex-flow: row wrap;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.orange-text {
  color: orange;
}

.blue-text {
  color: #0099ff;
}

.small-text {
  font-size: 15px;
}

.medium-text {
  font-size: 30px;
  font-weight: bolder;
}

.bold {
  font-weight: 500;
}

.bolder {
  font-weight: bolder;
}

.berlin {
  font-family: "Berlin Sans FB";
}

.forte {
  font-family: "Forte" !important;
}

.righteous {
  font-family: "Righteous";
}

.fugazOne {
  font-family: "fugazOne";
}

.arial {
  font-family: Arial, Helvetica, sans-serif;
}

.auto-height {
  min-height: 95vh;
  padding-bottom: 5vh;
  height: auto;
}

.auto-width {
  min-width: 80vw;
  width: auto;
}

.cursor {
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 8px;
  position: absolute;
  background-color: transparent;
  /*background-image: url("../images/home.jpg");*/
}

::-webkit-scrollbar-thumb {
  background: #0099ff;
  border-radius: 4px;
  -webkit-transition-duration: 0.3s; /* Safari */
  transition-duration: 0.3s;
}

::-webkit-scrollbar-thumb:active {
  background: #02a3e2;
}

@media only screen and (max-width: 1100px) {
  ::-webkit-scrollbar {
    width: 8px;
    position: absolute;
    display: none;
    background-color: transparent;
    /*background-image: url("../images/home.jpg");*/
  }
  
  ::-webkit-scrollbar-thumb {
    background: #0099ff;
    border-radius: 4px;
  }
}

@media only screen and (max-width: 1350px) {
  
  .home-wrapper {
    width: calc(100vw * 1 / 3 + 50px);
    height: 100vh;
  }
  
  .information-wrapper {
    width: calc((100vw) * 2 / 3 - 50px);
    height: 100vh;
    display: flex;
    flex-flow: row wrap;
    overflow-y: auto;
  }

  .information-section {
    width: calc((100% - 40px)) !important;
    height: auto !important;
  }

  .experiences {
    flex: none !important;
  }

  .skill-section {
    width: calc((100% - 80px)) !important;
    margin-top: 10px !important;
    margin-left: 20px;
    margin-bottom: 20px;
  }
}


@media only screen and (max-width: 1100px) {
  .wrapper {
    flex-flow: column;
  }
  
  .home-wrapper {
    width: calc(100vw);
    height: auto;
  }
  
  .information-wrapper {
    width: calc(100vw);
    height: auto;
    display: flex;
    flex-flow: row wrap;
    flex: none !important;
    overflow-y: auto;
  }

  .information-section {
    width: calc((100% - 40px)) !important;
    height: auto !important;
    min-width: 0px !important;
  }

  .information-section div {
    min-width: 0px !important;
  }

  .skill-section {
    width: calc((100% - 80px)) !important;
    height: auto !important;
    min-width: 0px !important;
  }

  .white-box {
    width: auto !important;
    max-width: none !important;
  }
}