.portfolio-section {
    position: relative;
    width: calc(100%);
    padding: 20px;
    background-size: cover;
    overflow: hidden;
}

.wave-background {
    width: calc(100% + 40px);
    margin-left: -20px;
    height: 320px;
    background: url(../images/blue-wave-3.svg) no-repeat; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

.portfolio-wrapper {
    width: calc(100%);
    background-color: #0099ff;
    margin-top: -100px;
    padding: 20px;
    border-radius: 0px 0px 20px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-left: -20px;
}

