html {
    position: relative;
}

a {
    text-decoration: none;
}

.bio-section {
    position: relative;
    width: calc(100% - 80px);
    height: calc(100% - 80px);
    background-color: rgba(255, 255, 255, 0.95);
    border-radius: 20px;
    padding: 20px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    overflow-y: overlay;
    overflow-x: hidden;
}

.bio-section:hover::-webkit-scrollbar {
    background-color: rgba(0, 0, 0, 0);
}

.bio-section::-webkit-scrollbar-thumb {
    background-color: rgba(0,0,0,0);
}

.bio-section:hover::-webkit-scrollbar-thumb {
    background-color: #0099ffc3;
}

.bio {
    padding: 10px;
    border-radius: 20px;
}

.bio h1 {
    margin: 0px;
}

.skill-portfolio-section {
    position: relative;
    width: calc((100% - 60px) * 2 / 3 - 40px);
    height: calc(100% - 80px);
    background-color: rgba(255, 255, 255, 0.95);
    border-radius: 20px;
    padding: 20px;
    display: flex;
    margin-left: 20px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    overflow-y: auto;
}

.section {
    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.section-row {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
}

.section-row-only {
    position: relative;
    display: flex;
    flex-flow: row wrap;
}

.section-column {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.wrap-row {
    flex-flow: row wrap;
}

.section-static {
    position: relative;
    min-height: 50vh;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.parallax::after {
    content: " ";
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;

    transform: translateZ(-1px) scale(1.5);
    /*background-attachment: fixed;*/
    background-size: cover;
    z-index: -50;
}

.bg1::after {
    background-image: url('../images/home.jpg');
    position: absolute;
    left: -5px;
}

.bg2::after {
    /*background-image: url('../images/about3.jpg');*/
    background-image: url('../images/Liquid-Cheese-3.svg');
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    transform: none;
}

.static {
    background-color: rgba(255, 255, 255, 1);
    box-shadow: 20px 20px 28px 0 rgba(0, 0, 0, 0.24), 28px 20px 38px 0 rgba(0, 0, 0, 0.23);
}

.download-image-container {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-self: flex-end;
    align-items: center;
    cursor: pointer;
    z-index: 100;
    margin-bottom: 10px;
}

.download-image {
    position: absolute;
    left: 60px;
    width: 35px;
    object-fit: contain;
    /*filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(192deg) brightness(99%) contrast(106%); /* #ffffff */
    filter: invert(63%) sepia(40%) saturate(5995%) hue-rotate(164deg) brightness(102%) contrast(105%); /* #0099ff */
    transition-duration: 0.3s;
}

.download-image-container span {
    position: relative;
    font-family: fugazOne;
    font-weight: bold;
    margin-left: 10px;
    visibility: hidden;
    opacity: 0;
    color: #0099ff;
    transition-duration: 0.3s;
    padding: 10px;
}

.download-image-container:hover img {
    left: -20px;
}

.download-image-container:hover span {
    opacity: 1;
    visibility: visible;
}

.header {
    margin-top: 20px;
    font-family: "fugazOne";
    font-size: 15px;
    font-weight: lighter;
    color: rgb(245, 245, 245);
}

.name {
    margin-top: 20px;
    font-family: "fugazOne";
    font-size: 28px;
    font-weight: bolder;
    color: white;
}

.bio-info {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: #5a5a5a;
    font-size: 15px;
}

.bio-info div {
    margin-top: 8px;
}

.bio-info div:first-child {
    margin-top: 0px;
}

.bio-info img {
    width: 25px;
    object-fit: contain;
    margin-right: 10px;
    filter: invert(32%) sepia(1%) saturate(0%) hue-rotate(142deg) brightness(100%) contrast(80%);
}

.submit-button {
    outline: none;
    background-color: #0099ff;
    border: 2px solid #0099ff;
    color: white;
    font-size: 15px;
    font-family: "Righteous";
    font-weight: 500;
    padding: 18px 25px;
    border-radius: 100px;
    -webkit-transition: all 0.2s ease-in-out; /* Safari */
    transition: all 0.2s ease-in-out;
    cursor: pointer;
}

.submit-button:hover {
    transform: translateY(-8px);
}

.submit-button:active {
    background-color: #02a2e2;
    border: 1.5px solid #02a2e2;
    color: white;
}

.submit-button:focus {
    outline: 0;
    box-shadow: none;
}

.button-orange {
    background-color: orange;
    border: 1.5px solid orange;
    color: white;
}

.button-orange:hover {
    background-color: white;
    border: 1.5px solid white;
    color: orange;
}

.button-orange:active {
    background-color: whitesmoke;
    border: 1.5px solid whitesmoke;
    color: orange;
}

.box-shadow {
    box-shadow: 3px 3px 6px 0 rgba(0, 0, 0, 0.2), 9px 4px 18px 0 rgba(0, 0, 0, 0.19);
}

.box-shadow-2 {
    /*box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.22), 0 7px 21px 0 rgba(0, 0, 0, 0.21);*/
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}